<template>
    <div id="bank" style="padding:5% 20%;width:50%;text-align:center">
      <el-form :model="form" label-width="100px" style="padding:4% 20% 5% 15%;background-color:#fff;border-radius:8px;">
        <div style="font-size:17px;margin:7% 0px;">
            <b>创建用户</b>
        </div>
        <el-form-item label="银行名称：">
            <el-input v-model="form.bankId" placeholder="请输入银行名称"></el-input>
        </el-form-item>
        <el-form-item label="用户名称：">
            <el-input v-model="form.accountId" placeholder="请输入用户名称"></el-input>
        </el-form-item>
        <el-form-item label="余额：">
            <el-input v-model="form.balance" placeholder="请设置余额"></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-button type="primary" @click="createUser">创建用户</el-button>
            <el-button type="success" @click="storage">保存记录</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>

<script>
export default {
  name: 'User',
  data () {
    return {
      form: {}
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var localStr = localStorage.getItem('user')
      if (localStr === null) {
        return
      }
      this.form = JSON.parse(localStorage.getItem('user'))
    },
    storage () {
      localStorage.setItem('user', JSON.stringify(this.form))
    },
    createUser () {
      var that = this
      console.log(this.form)
      this.form.balance = parseInt(this.form.balance)
      this.$axios.post('/v1/account', JSON.stringify(this.form))
        .then(function (response) {
          if (response.data.validCode === 0) {
            that.$message.success('创建成功')
          } else {
            that.$message.error(response.data.msg)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}
</script>



